.select-wrapper {
  position: relative;
}

.select-wrapper .icon {
  color: #009ede;
  height: 1rem;
  position: absolute;
  right: 1.125rem;
  top: 1.1875rem;
  transform: rotate(180deg);
  width: 1rem;
}

.select-wrapper--disabled {
  cursor: not-allowed;
}

.select-wrapper--disabled .icon {
  color: #b2b2b2;
}

.select {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-color: #fff;
  border: 0.125rem solid #e5e5e5;
  border-radius: 0.375rem;
  color: #767676;
  padding: 0.8125rem 2.8125rem 0.8125rem 0.9375rem;
  width: 100%;
}

.select:-webkit-autofill, .select:-webkit-autofill:hover, .select:-webkit-autofill:focus, .select:-webkit-autofill:active {
  background-color: #fff;
  background-image: none;
}

.select:hover, .select.is-hovered {
  color: #333f48;
}

.select:focus, .select.is-focused {
  border-color: #009ede;
  color: #333f48;
  outline: 0;
}

.select.error {
  border-color: #dd3817;
}

.select.success {
  border-color: #bad80a;
}

.select.disabled, .select.disabled:hover, .select.disabled:focus {
  background-color: #f8f8f8;
  border-color: #e5e5e5;
  color: #767676;
  cursor: not-allowed;
}

.select::-ms-expand {
  display: none;
}
